<template>
  <div class="bodyPadding">
    <el-date-picker
      v-model="dateBEST"
      type="daterange"
      range-separator=""
      start-placeholder=""
      end-placeholder=""
      :clearable='false'
      :picker-options="pickerOptions"
    ></el-date-picker>
    <el-input v-model="list.KeyWords" class="keyinput" placeholder=""></el-input>
    <el-select v-model="GoodsID" clearable placeholder="" filterable>
    <el-option
      v-for="item in GoodsList"
      :key="item.GoodsID"
      :label="item.GoodsName"
      :value="item.GoodsID">
    </el-option>
  </el-select>
  <el-select v-model="GoodsGroupsID" clearable placeholder="" filterable>
    <el-option
      v-for="item in GoodsGroupsList"
      :key="item.GoodsGroupsID"
      :label="item.GoodsGroupName"
      :value="item.GoodsGroupsID">
    </el-option>
  </el-select>
  <el-select v-model="orderType" placeholder="" filterable>
    <el-option
      v-for="item in OrderListSC"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
    <el-button icon="el-icon-search" circle @click="getReport"></el-button>
    <el-button @click="TableExcel"></el-button>
    <el-table :data="tableData" style="width: 100%" id="rebateSetTable">
      <el-table-column prop="GoodsGroupName" label=""></el-table-column>
      <el-table-column prop="GoodsName" label=""></el-table-column>
      <el-table-column prop="Transactiontimes" label=""></el-table-column>
      <el-table-column prop="SalesCount" label=""></el-table-column>
      <el-table-column prop="SalesAmount" label=""></el-table-column>
            <el-table-column prop="ProfitSum" label=""></el-table-column>
    </el-table>
  </div>
</template>
<script>
// import {
//   GetGoodsSalesReport,
//   GetStoreSaleReport,
//   GetSupplierSaleReport
// } from "@/api/dataReport";
import { GetSupplierUserList, GetUserList } from "@/api/Store";
import FileSaver from "file-saver";
import XLSX from "xlsx";
export default {
  name: "SaleReport",
  data() {
    return {
      pickerOptions: {
        shortcuts: [
          {
            text: "",
            onClick(picker) {
              //v-line,
              const end = new Date();
              let start = new Date();
              // start.setFullYear(2018);
              // start.setMonth(10);
              // start.setDate(1);
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 1);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              //v-line,
              const end = new Date();
              let start = new Date();
              // start.setFullYear(2018);
              // start.setMonth(10);
              // start.setDate(1);
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "",
            onClick(picker) {
              //v-line,
              const end = new Date();
              let start = new Date();
              // start.setFullYear(2018);
              // start.setMonth(10);
              // start.setDate(1);
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      list: {
        Offset: 0,
        Rows: 10,
        KeyWords: "",
        BeginTime: "",
        EndTime: ""
      },
      dateBEST: "",
      tableData: [],
      GoodsGroupsList:[],
      GoodsList:[],
      GoodsID:'',
      GoodsGroupsID:'',
      SupplierID:"",
      UserID:"",
      orderType:1,
      OrderListSC:[
        {label:"",value:1},
        {label:"",value:2},
        {label:"",value:3},
        {label:"",value:4},
        {label:"",value:5},
        {label:"",value:6},
        {label:"",value:7},
        {label:"",value:8},
      ],
    };
  },
  created() {

    this.SupplierID=JSON.parse(localStorage.getItem('userInfoS')).SuppliersID;
      this.Initialization();
  },
  methods: {
    //
    getReport() {
      if(!this.dateBEST){
        this.dateBEST=['',''];
      }
      this.list.BeginTime = this.dateBEST[0];
      this.list.EndTime = this.dateBEST[1];
      this.$request.GetGoodsSalesReport(this.list,this.GoodsID,this.GoodsGroupsID,this.orderType,this.SupplierID,this.UserID).then(res => {
        this.tableData = res.content;
        let content=res.content;
        let arr=[];
        let list=[];
        for(let i=0;i<content.length;i++){
          let {GoodsName,GoodsID,GoodsGroupsID,GoodsGroupName}=content[i];
          arr.push({GoodsName,GoodsID});
          list.push({GoodsGroupsID,GoodsGroupName});
        }
        var obj={};
        list=list.reduce((one,two)=>{
          //one  twolist
          obj[two.GoodsGroupsID]?'':obj[two.GoodsGroupsID]=true&&one.push(two);
          //objidobjidtrueonetwo
          return one;
        },[]);
        this.GoodsGroupsList=list;
        this.GoodsList=arr;
      });
    },
    Initialization() {
      let start = new Date();
      let end = new Date();
      start.setTime(start.getTime() - 1000 * 60 * 60 * 24 * 7);
      this.dateBEST = [start, end];
      this.getReport();
    },
    TableExcel() {
      let wb = XLSX.utils.table_to_book(
        document.querySelector("#rebateSetTable")
      );
      /* get binary string as output */
      let wbout = XLSX.write(wb, {
        bookType: "xlsx",
        bookSST: true,
        type: "array"
      });
      try {
        FileSaver.saveAs(
          new Blob([wbout], { type: "application/octet-stream" }),
          ".xlsx"
        );
      } catch (e) {
        if (typeof console !== "undefined") console.log(e, wbout);
      }
      return wbout;
    }
  }
};
</script>
<style scoped>
.bodyPadding {
  padding: 15px;
}
.keyinput{
  width:200px;
}
</style>



