export async function GetSupplierUserList(options){//
    if(options.BeginTime){
      let query={
        'StartTime':options.BeginTime,
        'EndTime':options.EndTime
      }
      query=SetDate(query);
      options.BeginTime=query.StartTime;
      options.EndTime=query.EndTime;
    }

  return await postd("Goods/GetSupplierUserList","",options);
}
